*{margin: 0;padding: 0; box-sizing: border-box;}


@import url('https://fonts.googleapis.com/css2?family=DM+Sans:opsz@9..40&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Merriweather&display=swap');
:root {
    --primary-colors-white: #FFF;
    --general-1-primary: #A6876A;
    --general-1-secondary: #0E163D;
    --general-1-secondary-variant: #414562;
    --general-white: #FFF;
    --elements-neutral-1: #597393;
    --elements-neutral: #878995;
    --elements-neutral-2: #BFD4E4;
    --elements-neutral-4: #F2F7F9;
}


body{
    display: flex;
    width: 1920px;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    background: var(--primary-colors-white, #FFF);
    box-sizing: border-box;
}

.top{
    width: 1920px;
    height: 540px;
    flex-shrink: 0;
}


header{
    position: absolute;
    z-index: 999;
    display: flex;
    width: 1920px;
    height: 140px;
    padding: 24px 375px;
    justify-content: space-between;
    align-items: center;
    flex-shrink: 0;
    box-sizing: border-box;
}

.logo{
    display: flex;
    align-items: center;
    gap: 2px;
}

.logomark{
    width: 36px;
    height: 36px;
    fill: var(--general-1-primary, #A6876A);

}

.logotype{
    width: 130.96px;
    height: 27.84px;
    fill: var(--general-1-secondary, #0E163D);
}

.mobi{
    display: none;
}

.main{
    display: flex;
    justify-content: flex-end;
    align-items: center;
    gap: 25px;
}

.menu{
    display: flex;
    align-items: center;
    gap: 33px;
}

.home{
    display: flex;
    align-items: center;
    gap: 4px;
}

.home:hover .text,
.home:hover .down {
    cursor: pointer;
    fill: #A6876A;
    color: #A6876A;
}

.text{
    color: var(--general-1-secondary-variant, #414562);
    text-align: right;
    font-feature-settings: 'clig' off, 'liga' off;

    /* Button – DM Sans, 16px */
    font-family: DM Sans;
    font-size: 16px;
    font-style: normal;
    font-weight: 700;
    line-height: 22px; /* 137.5% */
}

.down{
    width: 16px;
    height: 16px;
    fill:#414562;
}

.divider{
    width: 1px;
    height: 20px;
}


.icons{
    display: flex;
    width: 24px;
    height: 24px;
    padding: 3px;
    justify-content: center;
    align-items: center;
}

.search{
    width:20px;
    height:20px;
    stroke:#414562;
    stroke-width:2;
    stroke-linecap:round;
    stroke-linejoin:round;
}

.shop{
    width: 24px;
    height: 23px;
    stroke:#414562;
    stroke-width: 2px;
    stroke-linecap:round;
    stroke-linejoin:round;
    flex-shrink: 0;
}

.shop:hover,
.search:hover,
.circle:hover {
    transform: scale(1.1);
    transition:200ms;
}


#pic{
    position: absolute;
    top: 0;
    left: 575px;
    width: 1345px;
    height: 540px;
    flex-shrink: 0;
    background-color: lightgray; 
    background-size: 50% 75%;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
}

.fronto{
    position: absolute;
    margin: 270px 725px 78px 375px;
    width: 820px;
}

.front{
    color: var(--general-1-secondary, #0E163D);
    font-feature-settings: 'clig' off, 'liga' off;
    /* Additional Headings/Additional Heading 1 */
    font-family: DM Sans;
    font-size: 96px;
    font-style: normal;
    font-weight: 700;
    line-height: 96px; /* 100% */
    letter-spacing: -4px;
}


#navigation{
    height: 70px;
    flex-shrink: 0;
    display:flex;
    gap: 10px;
    position:absolute;
    margin: 410px 455px 60px 1395px;
}

.circle{
    width: 70px;
    height: 70px;
    flex-shrink: 0;
    fill: var(--general-white, #FFF);
    display: flex;
    justify-content: center;
    align-items: center;
}

.navig{
    width: 24px;
    height: 24px;
    flex-shrink: 0;
    position: absolute;
}





/* тут второй компонент */


.second{
    display: flex;
    width: 1920px;
    padding: 89px 375px 90px 375px;
    align-items: center;
    gap: 90px;
    box-sizing: border-box;
}

.topic{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 12px;
    flex: 1 0 0;
    align-self: stretch;
}

.topic0 {
    align-self: stretch;
    font-feature-settings: 'clig' off, 'liga' off;

    /* Headlines/H6 – DM Sans, 18px */
    font-family: DM Sans;
    font-size: 18px;
    font-style: normal;
    font-weight: 700;
    line-height: 24px; /* 133.333% */
    letter-spacing: -0.09px;
}
.h0 {
    color: var(--general-1-primary, #A6876A);
    fill: #A6876A;
}

.line{
    height: 1px;
    align-self: stretch;
    fill: #BFD4E4;
}

.topic2{
    align-self: stretch;
    color: var(--elements-neutral-1, #597393);
    font-feature-settings: 'clig' off, 'liga' off;

    /* Body/B2 – Merriweather, 16px */
    font-family: Merriweather;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 26px; /* 162.5% */
}


#scroll{
    display:flex;
    width: 26px;
    height: 140px;
    padding-bottom: 0px;
    flex-direction:column;
    align-items: center;
    gap: 14px;
    flex-shrink: 0;
}

#scroll:hover{
    cursor:s-resize;
    gap: 20px;
}

.scrollText{
    width: 93px;
    height: 26px;
    transform: rotate(90deg);
    color: var(--elements-neutral, #878995);
    text-align: right;
    font-feature-settings: 'clig' off, 'liga' off;
    /* Body/B2 – Merriweather, 16px */
    font-family: Merriweather;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    margin-bottom: 40px;
}

.scroll-icon{
    width: 24px;
    height: 24px;
    flex-shrink: 0;
}



/* третий компонент */

.about{
    display: flex;
    justify-content: center;
    align-items: center;

    width: 1920px;
    height: 780px;
    background: var(--general-1-secondary, #0E163D);
    box-sizing: border-box;
    position: relative;
}

.aboutText{
    color: var(--general-1-primary, #A6876A);
    font-feature-settings: 'clig' off, 'liga' off;
    font-family: DM Sans;
    font-size: 16px;
    font-style: normal;
    font-weight: 700;
    line-height: 22px;
    text-transform: uppercase;

    position: absolute;
    top: 160px;
    left: 375px;
}

.champ{
    margin: 140px 0 388px 0;
}

.one{
    width: 770px;
    color: var(--general-white, #FFF);
    font-feature-settings: 'clig' off, 'liga' off;
    /* Additional Headings/Additional Heading 2 */
    font-family: DM Sans;
    font-size: 78px;
    font-style: normal;
    font-weight: 700;
    line-height: 84px; /* 107.692% */
    letter-spacing: -2.73px;
}


.description{
    display: flex;
    position: absolute;
    margin: 453px 375px 177px 575px;
    width: auto;
}

.aside{
    
    width: 320px;
    color: var(--general-white, #FFF);
    font-feature-settings: 'clig' off, 'liga' off;
    font-family: DM Sans;
    font-size: 24px;
    font-style: normal;
    font-weight: 700;
    line-height: 30px;
    letter-spacing: -0.24px;
    margin-right: 80px;
}

.wrapper{
    color: var(--elements-neutral-2, #BFD4E4);
    font-feature-settings: 'clig' off, 'liga' off;
    font-family: Merriweather;
    font-style: normal;
    font-weight: 400;
    line-height: 24px;
}

.wrapper p{
    height: 72px;
    width: 170px;
    font-size: 14px;
    margin-top: 26px;
}

.box{
    color: var(--general-1-primary, #A6876A);
    font-feature-settings: 'clig' off, 'liga' off;
    font-family: DM Sans;
    font-size: 48px;
    font-style: normal;
    font-weight: 700;
    line-height: 54px;
    letter-spacing: -1.2px;

}

.signs{
    margin: 0 6px 0 6px;
    color: var(--general-white, #FFF);
    font-feature-settings: 'clig' off, 'liga' off;
    font-family: DM Sans;
    font-size: 24px;
    font-style: normal;
    font-weight: 700;
}

/* компонент */


.ourservise{
    width: 1920px;
    height: 1090px;
    background: var(--general-white, #FFF);
    display: flex;
    flex-direction: column;
    position: relative;
}

.item_1{
    width: 140px;
    height: 22px;
    color: var(--general-1-primary, #A6876A);
    font-feature-settings: 'clig' off, 'liga' off;
    font-family: DM Sans;
    font-size: 16px;
    font-style: normal;
    font-weight: 700;
    line-height: 22px;
    text-transform: uppercase;

    margin: 133px 0 0 375px;
}
.item_2{
    width: 870px;
    height: 108px;
    color: var(--general-1-secondary, #0E163D);
    font-feature-settings: 'clig' off, 'liga' off;
    font-family: DM Sans;
    font-size: 48px;
    font-style: normal;
    font-weight: 700;
    line-height: 54px;
    letter-spacing: -1.2px;
    margin: 30px 0 0 375px;
}

.item_3{
    width: 670px;
    height: 60px;
    color: var(--elements-neutral, #878995);
    font-feature-settings: 'clig' off, 'liga' off;
    font-family: Merriweather;
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: 30px;
    margin: 50px 0 43px 375px;
}

.button{
    display: flex;
    width: 170px;
    height: 50px;
    padding-right: 0px;
    align-items: center;
    justify-content: center;
    gap: 15px;
    flex-shrink: 0;
    border: 2px solid var(--general-1-primary, #A6876A);
    background: var(--general-white, #FFF);
    top: 348px;
    right: 675px;
    bottom: 692px;
    left: 1075px;
    z-index: 999;
    position: absolute;
}

.button:hover{
    background-color: var(--general-1-primary, #A6876A);
    cursor: pointer;
    color: white;
}
.button:active{
    background-color: #81654d;
}

.item_4{
    width: 170px;
    flex-shrink: 0;
    text-align: center;
    font-feature-settings: 'clig' off, 'liga' off;
    font-family: DM Sans;
    font-size: 16px;
    font-style: normal;
    font-weight: 700;
    line-height: 22px;
}

.sitemap{
    background-image: url('../img/Img.jpeg');
    background-color: lightgray;
    background-size: 50%;
    background-size: cover;
    background-repeat: no-repeat;



    width: 540px;
    height: 823px;
    flex-shrink: 0;
    position: absolute;
    z-index: 990;
    left: 1380px;
}



.rectangle{
    width: 1920px;
    height: 377px;
    flex-shrink: 0;
    background: var(--elements-neutral-4, #F2F7F9);
    position: relative;
    padding: 70px 440px 67px 375px;

    display: flex;
    flex-direction: row;
}


.frame{
    display: flex;
    width: 410px;
    height: 20px;
    flex-direction: column;
    align-items: flex-start;
    gap: 40px;
}

.arc{
    position: absolute;
    z-index: 998;
    top: 90px;
    right: 440px;
    bottom: 87px;
    left: 1280px;
}

.bigLink{
    display: flex;
    align-items: flex-start;
    align-self: stretch;
}
.frameText{
    flex: 1 0 0;
    color: var(--general-1-secondary, #0E163D);
    font-feature-settings: 'clig' off, 'liga' off;
    font-family: DM Sans;
    font-size: 24px;
    font-style: normal;
    font-weight: 700;
    line-height: 30px;
    letter-spacing: -0.24px;
}
.frameNumb{
    width: 30px;
    color: var(--general-1-primary, #A6876A);
    font-feature-settings: 'clig' off, 'liga' off;
    font-family: DM Sans;
    font-size: 12px;
    font-style: normal;
    font-weight: 500;
    line-height: 16px;
}




.ourClients{
    display: inline-flex;
    padding: 98px 375px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 10px;
    background: var(--general-white, #FFF);
}

.logoClients{
    display: flex;
    width: 1170px;
    height: 64px;
    gap: 130px;
}
.logos{
    fill: var(--elements-neutral-1, #597393);
    opacity: 0.4;
    flex-shrink: 0;
}
