
@media (max-width: 375px) {
    body{
        display: flex;
        width: 375px;
        /* overflow: hidden; */
        flex-direction: column;
        justify-content: center;
        align-items: flex-start;
        background: rgba(176, 84, 84, 0.516);
        /* background: var(--General-White, #FFF); */
        box-sizing: border-box;
    }
    .top{
        display: flex;
        width: 375px;
        height: 812px;
        flex-direction: column;
        /* justify-content: center; */
        align-items: flex-start;
        background: var(--General-White, #FFF);
        position: relative;
    }
    header{
        position: static;
        display: flex;
        width: 375px;
        height: 80px;
        padding: 24px 30px;
        justify-content: space-between;
        align-items: flex-end;
        flex-shrink: 0;
        margin-bottom: 177px;
        box-sizing: border-box;
    }

    .mobi{
        display: block;
        width: 24px;
        height: 24px;
    }
    .mobi:hover{
        transform: scale(1.1);
    }

    .main{
        display: block;
        position: absolute;
        z-index: 999;
        top: 0;
        right: 40px;
        width: 150px;
        padding: 15px;
        background: var(--General-White, #FFF);
        box-shadow: 0px 10px 20px 0px rgba(14, 22, 61, 0.20);
    }
    .menu{
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        gap: 0;
    }
    .home{
        display: flex;
        padding: 11px 5px 11px 15px;
        align-items: center;
        gap: 10px;
        align-self: stretch;
    }

    .text{
        flex: 1 0 0;
        color: var(--General-1-Secondary-variant, #414562);
        font-feature-settings: 'clig' off, 'liga' off;

        text-align: left;
        font-family: DM Sans;
        font-size: 16px;
        font-style: normal;
        font-weight: 700;
        line-height: 22px; /* 137.5% */
    }


    .divider,
    .icons,
    .down{
        display: none;
    }





    #pic{
        width: 375px;
        height: 555px;
        flex-shrink: 0;
        position: static;
        background-color: lightgray;
        background-position: -296px -77.416px;
        background-size: 198.382% 113.949%;
        background-repeat: no-repeat;
        background-image: url(../img/mobi.jpeg);
    }

    .fronto{
        position: absolute;
        z-index: 998;
        width: 315px;
        margin: 160px 30px 0 30px;
    }
    .front{
        width: 260px;
        color: var(--General-1-Secondary, #0E163D);
        font-feature-settings: 'clig' off, 'liga' off;
        font-family: 'DM Sans', sans-serif;
        font-size: 48px;
        font-style: normal;
        font-weight: 700;
        line-height: 54px;
        letter-spacing: -1.2px;
    }

    #navigation{
        position: absolute;
        height: 70px;
        flex-shrink: 0;
        display:flex;
        gap: 10px;
        z-index: 1000;
        margin: auto;
        right: 30px;
        bottom: 30px;
    }

    .second{
        display: none;
    }


    

    .about{
        width: 375px;
        height: 1110px;
        padding: 120px 30px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        gap: 40px;
        align-items: center;
        background: var(--General-1-Secondary, #0E163D);
        position: relative;
    }

    .aboutText{    
        position: absolute;
        top: 120px;
        left: 30px;
    }

    .champ{
        margin: 52px 0 0 0;
    }

    .one{
        width: 315px;
        font-size: 42px;
        line-height: 48px; /* 114.286% */
        letter-spacing: -0.84px;
    }
    .description{
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        gap: 40px;
        position: relative;
        margin: auto;
        width: 315px;
    }
    .aside{
        width: 300px;
        letter-spacing: -0.24px;
        margin: auto;
    }
    .wrapper{
        color: var(--elements-neutral-2, #BFD4E4);
        font-feature-settings: 'clig' off, 'liga' off;
        font-family: Merriweather;
        font-style: normal;
        font-weight: 400;
        line-height: 24px;
        margin: auto;
    }
    .wrapper p{
        height: 48px;
        width: 315px;
        font-size: 14px;
        margin-top: 10px;
    }




    .ourservise{
        width: 375px;
        height: 1300px;
        background: var(--General-White, #FFF);
        display: flex;
        flex-direction: column;
        position: relative;
    }

    .item_1{
        width: 130px;
        font-family: DM Sans;
        position: absolute;
        margin: 120px 0px 0px 30px;

    }
    .item_2{
        width: 315px;
        height: 144px;
        font-size: 30px;
        line-height: 36px;
        letter-spacing: -0.45px;
        margin: 172px 30px 0px 30px;
    }
    
    .item_3{
        width: 315px;
        height: 78px;
        font-size: 16px;
        line-height: 26px; /* 162.5% */
        margin: 30px 30px 30px 30px;
    }
    .button{
        position: static;
        margin: 0px 175px 60px 30px;
    }

    .rectangle{
        width: 375px;
        height: 522px;
        padding: 60px 30px;
        display: flex;
        flex-direction: column;
    }

    .frame{
        height: auto;
        display: flex;
        width: 315px;
        gap: 30px;
    }
    .frameText{
        flex: 1 0 0;
        font-size: 18px;
        line-height: 24px;
        letter-spacing: -0.09px;
    }

    .ourClients,
    .sitemap,
    .arc {
        display: none;
    }





    .case{
        width: 375px;
        height: 1630px;
        margin-top: 0px;
    }

    #studiesPic {
        position: relative;
        display: flex;
        flex-direction:column;
        width: 375px;
        height: 1780px;
        
    }

    .overline{
        position: absolute;
        top: 40px;
        left: 30px;
    }
    .compo4 {
        width: 375px;
        height: 325px;
        display: flex;
        flex-direction: column;
    }

    .rec1726 {
        width: 375px;
        height: 325px;
        padding: 20px;
        display: flex;
        flex-direction: column;
    }
    .frame5 {
        width: 295px;
        height: 244px;
    }

    .rec {
        /* float: bottom; */
        width: 375px;
        height: 1px;
    }
    .sales {
        align-self: stretch;
        font-size: 18px;
        line-height: 24px; /* 133.333% */
        letter-spacing: -0.09px;
     }
     .webuild {
        font-size: 16px;
        line-height: 26px; /* 162.5% */
     }

     .morebutton {
        align-items: flex-end;
    } 

}