.case{
    width: 1920px;
    height: 780px;
    background: var(--General-1-Secondary-variant, #414562);
    overflow: hidden;
    margin-top: 6px;
}
#studiesPic {
    position: relative;
    display: flex;
    flex-direction:row;
    width: 1919px;
    height: 1280px;
    flex-shrink: 0;
    opacity: 0.9;
}

.compo4 {
    width: 383px;
    height: 780px;
    flex-shrink: 0;
    display: flex;
    flex-direction: row;
}
.rec1726 {
    width: 383px;
    height: 780px;
    flex-shrink: 0;
    padding: 40px;
    display: flex;
    flex-direction: row;
}
.rec1726:hover {
    background: rgba(0, 0, 0, 0.2);
}

.frame5 {
    display: flex;
    width: 303px;
    height: 699px;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-start;
    gap: 20px;
    flex-shrink: 0;
}

.rec {
    width: 1px;
    height: 780px;
    flex-shrink: 0;
    opacity: 0.25;
    background: var(--General-White, #FFF);
    /* float: right; */
}

.frame21 {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 20px;
    align-self: stretch;
}

.sales {
    align-self: stretch;
    color: var(--General-White, #FFF);
    font-feature-settings: 'clig' off, 'liga' off;
    font-family: DM Sans;
    font-size: 24px;
    font-style: normal;
    font-weight: 700;
    line-height: 30px;
    letter-spacing: -0.24px;
 }
 .webuild {
    align-self: stretch;
    color: var(--General-White, #FFF);
    font-feature-settings: 'clig' off, 'liga' off;
    font-family: Merriweather;
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: 30px;
 }

 .morebutton {
    display: flex;
    gap: 5px;
    border: none;
    background-color: transparent;
} 

.medium {

    color: var(--General-1-Primary, #A6876A);
    font-feature-settings: 'clig' off, 'liga' off;

    /* Button – DM Sans, 16px */
    font-family: DM Sans;
    font-size: 16px;
    font-style: normal;
    font-weight: 700;
    line-height: 22px; /* 137.5% */
}
.frame287 {

    width: 24px;
    height: 24px;
    padding: 10px 9px 10px 3px;
    position: relative;
}
.arrow {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-shrink: 0;
    width: 22px;
    height: 22px;
    position: absolute;
    right: 0;
    top: 0;
    
}
.rec1725 {
    height: 2px;
    flex-shrink: 0;
    background: var(--General-1-Primary, #A6876A);
}
.frame287:hover{
    width:30px;
    transition: 0.5s;
}
.overline{
    position: absolute;
    top: 100px;
    left: 375px;
    color: var(--General-White, #FFF);
    font-feature-settings: 'clig' off, 'liga' off;

    /* Overline – DM Sans, 16px */
    font-family: DM Sans;
    font-size: 16px;
    font-style: normal;
    font-weight: 700;
    line-height: 22px; /* 137.5% */
    text-transform: uppercase;
}